import React, { useState,useRef,useEffect } from 'react';
import dragImg from '../../images/drag.png'
import { Image } from "react-bootstrap";
import { makeDroppable, callwebService , getTenant } from "../../utils/helpers";

import axios from 'axios';

// Create headers using environment variables


const API = () => {
    const [jsonData, setJsonData] = useState(null);
    const dropContainerRef = useRef(null);
    const [isDropped, setIsDropped] = useState(false);
    const myHeaders = {
        "client_id": "72dca7c76f0347f2b9463f95cd8ccd3a",
        "client_secret": "11c1DAF014104f1eA099B9a52f649517",
        "BusinessGroup": "Emerson",
        "SenderId": "PLM",
        "TargetId": "CMAP",
    };
    const getCookies = () => {
        return document.cookie;
    };
    const getCMAPData = (name) => {
        axios.get("https://aspmsint.emerson.com/aslplm-p-items-v3/api/emerson?bom="+name, {

            headers: {
                ...myHeaders,
                'Cookie': getCookies()
            },
            withCredentials: true
        })

            .then((response) => {
                setJsonData(response);
            }).catch((error) => {
            console.error(error);
        });
    };
    useEffect(() => {
        const dropContainer = dropContainerRef.current;
        if (dropContainer) {
          makeDroppable(dropContainer, handleDrop);
        }
      }, []);


    const handleDrop = async (dropObject) => {
        const title = dropObject.data?.items[0]?.displayName;
        getCMAPData(title)
        setIsDropped(true);
    };

    return (
        <div className="api-container">
            {!isDropped && (
        <div
          ref={dropContainerRef}
          className="droppable-container mt-4"
        >
          <Image
            style={{ width: "90px", height: "90px" }}
            src="https://thewhitechamaleon.github.io/testrapp/images/drag.png"
            alt="Drag and Drop Physical Product"
            className="search-icon"
          />
          <span className="drag-and-drop-text">Drag and Drop</span>y

        </div>
      )}

            {jsonData && (
                <div className="json-preview">
                    {jsonData}
                </div>
            )}
        </div>
    );
};

export default API;
